export default Parse;

export const createOneProduct = (modal, storeId) => {
  Parse.Cloud.run("createProduct", {
    storeId,
    modals: [modal],
  });
};

export const updateOneProduct = (modal) => {

  Parse.Cloud.run("updateProduct", {
    modal: modal,
    storeId: modal.storeId,
    productId: modal.productId,
  });
};

export const deleteOneProduct = (modal) => {

  Parse.Cloud.run("deleteOneProduct", {
    storeId: modal.storeId,
    productId: modal.productId
  });
};


export const createStore = (modal) => {
  if (modal.objectId) {
    Parse.Cloud.run("updateStore", {
      modals: [modal],
    });
  } else {
    Parse.Cloud.run("createStoreForUser", {
      modals: [modal],
    });
  }
};



export const findProduct = async (store) => {
  return (
    store &&
    (Parse.Cloud.run("findMyStoreProduct", {
      storeId: store.id,
    }))
  );
};
export const findMyStore = () => {
  return Parse.Cloud.run("findstore");
};

export const callCloud = (action, params) => {
  return Parse.Cloud.run(action, {
    params
  });
}

export const crud = (className, modal, mode) => {
  if(mode === 'create') {
    return Parse.Cloud.run(`create${className}`, {
      modals: [modal]
    });
  }
  if(mode === 'read') {
    return Parse.Cloud.run(`find${className}`, {
      modals: [modal]
    });
  }
  if(mode === 'update') {
    return Parse.Cloud.run(`update${className}`, {
      modals: [modal]
    });
  }
  if(mode === 'delete') {
    return Parse.Cloud.run(`delete${className}`, {
      modals: [modal]
    });
  }
  
}